.darkMode, .darkMode * {
    color: white;
    background-color: var(--color);
}

.toast-success,.toast-warning, .toast-info,.toast-error {
    background: white;
}


#toast-container {

    .toast-error {
    
        .toast-progress {
            background-color: #bd362f;
        }
    
        .toast-message {
    color: #bd362f;
    }

    

    background-image: url(data:image/png;base64,)!important;
}

.toast-warning {
    .toast-progress {
        background-color: #f89406;
    }

    .toast-message {
color: #f89406;
}

background-image: url(data:image/png;base64,)!important;

}

.toast-info {
    .toast-progress {
        background-color: #2f96b4;
    }

    .toast-message {
color: #2f96b4;
}

background-image: url(data:image/png;base64,)!important;

}

.toast-success {
    .toast-progress {
        background-color: #51a351;
    }

    .toast-message {
color: #51a351;
}

background-image: url(data:image/png;base64,)!important;

}
}